import { post } from './ApiSender';
import { AmenityApi } from './constant';

export function createAmenity(payload) {
    return post(AmenityApi.createAmenity, payload);
}

export function getAllAmenity(payload) {
    return post(AmenityApi.getAllAmenity, payload);
}

export function toggleStatusAmenity(payload) {
    return post(AmenityApi.toggleStatusAmenity, payload);
}

export function updateAmenity(payload) {
    return post(AmenityApi.updateAmenity, payload);
}
